.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.modal-content {
  background: white;
  border-radius: 12px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  width: 100%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
}
.modal-content::-webkit-scrollbar {
  width: 0;
  display: none;
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem 1.5rem 1rem;
  border-bottom: 1px solid #e5e7eb;
  background: #f9fafb;
  border-radius: 12px 12px 0 0;
}
.modal-header .modal-title {
  display: flex;
  align-items: center;
  font-size: 1.25rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0;
}
.modal-header .modal-title svg {
  color: #6b7280;
}
.modal-header .close-btn {
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 6px;
  transition: all 0.2s ease;
}
.modal-header .close-btn:hover {
  background-color: #f3f4f6;
  color: #374151;
}
.modal-header .close-btn svg {
  width: 1.25rem;
  height: 1.25rem;
}

.modal-body {
  padding: 1.5rem;
}

.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem 1.5rem;
  text-align: center;
  color: #6b7280;
}
.loading-state .spinner {
  width: 2rem;
  height: 2rem;
  border: 3px solid #e5e7eb;
  border-top: 3px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}
.loading-state p {
  margin: 0;
  font-size: 1rem;
  font-weight: 500;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.edit-form .form-section {
  margin-bottom: 2rem;
}
.edit-form .form-section:last-child {
  margin-bottom: 0;
}
.edit-form .form-section .section-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 1rem 0;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #e5e7eb;
}
.edit-form .form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
}
@media (max-width: 768px) {
  .edit-form .form-row {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
}
.edit-form .form-group {
  display: flex;
  flex-direction: column;
}
.edit-form .form-group .form-label {
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.5rem;
}
.edit-form .form-group .form-label::after {
  content: " *";
  color: #dc2626;
  display: none;
}
.edit-form .form-group .form-label.required::after {
  display: inline;
}
.edit-form .form-group .form-input,
.edit-form .form-group .form-select,
.edit-form .form-group .form-textarea {
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 0.875rem;
  transition: all 0.2s ease;
  background-color: white;
}
.edit-form .form-group .form-input:focus,
.edit-form .form-group .form-select:focus,
.edit-form .form-group .form-textarea:focus {
  outline: none;
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}
.edit-form .form-group .form-input.error,
.edit-form .form-group .form-select.error,
.edit-form .form-group .form-textarea.error {
  border-color: #dc2626;
  box-shadow: 0 0 0 3px rgba(220, 38, 38, 0.1);
}
.edit-form .form-group .form-input::placeholder,
.edit-form .form-group .form-select::placeholder,
.edit-form .form-group .form-textarea::placeholder {
  color: #9ca3af;
}
.edit-form .form-group .form-textarea {
  resize: vertical;
  min-height: 80px;
}
.edit-form .form-group .error-message {
  color: #dc2626;
  font-size: 0.75rem;
  margin-top: 0.25rem;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}
.edit-form .form-row:has(.form-group:only-child) {
  grid-template-columns: 1fr;
}

.modal-footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1rem 1.5rem 1.5rem;
  border-top: 1px solid #e5e7eb;
  background: #f9fafb;
  border-radius: 0 0 12px 12px;
}
.modal-footer .btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  text-decoration: none;
  min-width: 120px;
}
.modal-footer .btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.modal-footer .btn svg {
  width: 1rem;
  height: 1rem;
}
.modal-footer .btn-secondary {
  background-color: #f3f4f6;
  color: #374151;
  border: 1px solid #d1d5db;
}
.modal-footer .btn-secondary:hover:not(:disabled) {
  background-color: #e5e7eb;
  border-color: #9ca3af;
}
.modal-footer .btn-primary {
  background-color: #2563eb;
  color: white;
}
.modal-footer .btn-primary:hover:not(:disabled) {
  background-color: #1d4ed8;
  transform: translateY(-1px);
}
.modal-footer .btn-primary:disabled {
  background-color: #9ca3af;
}

@media (max-width: 640px) {
  .modal-overlay {
    padding: 0.5rem;
  }
  .modal-content {
    max-height: 95vh;
  }
  .modal-header {
    padding: 1rem 1rem 0.75rem;
  }
  .modal-header .modal-title {
    font-size: 1.125rem;
  }
  .modal-body {
    padding: 1rem;
  }
  .modal-footer {
    padding: 0.75rem 1rem 1rem;
    flex-direction: column;
  }
  .modal-footer .btn {
    width: 100%;
    justify-content: center;
  }
  .edit-form .form-row {
    gap: 0.5rem;
    margin-bottom: 0.75rem;
  }
  .edit-form .form-section {
    margin-bottom: 1.5rem;
  }
  .edit-form .form-section .section-title {
    font-size: 1rem;
    margin-bottom: 0.75rem;
  }
}
.modal-overlay {
  animation: fadeIn 0.2s ease-out;
}

.modal-content {
  animation: slideIn 0.2s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
.btn:disabled {
  position: relative;
}
.btn:disabled .animate-spin {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
